<template>
  <div>
    <el-radio v-model="permissions" label="superAdmin" border></el-radio>
    <el-radio v-model="permissions" label="admin" border></el-radio>
    <el-radio v-model="permissions" label="ordinary" border></el-radio>
    <div style="margin: 50px 0px;">
      [:  "{{options.permissions}}",    :   "{{options.role}}"]
    </div>
    <p></p>
    <p>[""]</p>
    <p>["", ]</p>
    <p>[""]</p>
  </div>
</template>

<script>
  export default {
    name: 'permissions',
    data () {
      return {
        permissions: 'superAdmin',
        options: {
          role: 'superAdmin',
          permissions: ''
        }
      };
    },
    mounted (){
      this.$store.dispatch('setRole', this.options)
    },
    watch: {
      permissions (newQuestion, oldQuestion) {
        switch (newQuestion) {
          case 'superAdmin':
            this.options = {
              role: 'superAdmin',
              permissions: ''
            }
            break
          case 'admin':
            this.options = {
              role: 'admin',
              permissions: ''
            }
            break
          case 'ordinary':
            this.options = {
              role: 'ordinary',
              permissions: ''
            }
        }
        this.$store.dispatch('setRole', this.options)
        this.$store.dispatch('removeOtherTab', {tabItem: {
            title: 'control',
            path: '/permissions'
          }, router: this.$router})
      }
    }
  }
</script>

<style scoped>

</style>



