<template>
  <div>
    <h3 class="loginTitle">vue-xuadmin</h3>
    <div class="loginBox">
      <el-form :model="loginForm" status-icon label-width="100px" class="demo-ruleForm">
        <el-form-item>
          <span class="ico"><i class="fa fa-user fa-lg"></i></span><el-input type="text" v-model="loginForm.username" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <span class="ico"><i class="fa fa-unlock-alt fa-lg"></i></span><el-input type="password" v-model="loginForm.password" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="subBtn" type="primary" @click="submitForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      loginForm: {
        username: 'vue-xuadmin',
        password: '123456'
      }
    }
  },
  methods: {
    submitForm () {
      let that = this
      if (this.loginForm.username === '' || this.loginForm.password === '') {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        })
        return false
      } else {
        //  username  token  store token 
        that.$store.dispatch('setToken', that.loginForm.username).then(() => {
          that.$router.push({path: '/'})
        }).catch(res => {
          that.$message({
            showClose: true,
            message: res,
            type: 'error'
          })
        })
      }
    },
    message() {
      const h = this.$createElement;
      this.$notify({
        title: '',
        message: h('i', { style: 'color: teal'}, ' token'),
        duration: 6000
      });
    },
  },
  mounted() {
    this.message()
  }
}
</script>
<style lang="scss">
body{
  background: #2d3a4b;
  .loginTitle{
    margin-top: 230px;
    font-size: 30px;
    color: #ffffff;
    text-align: center;
  }
  .loginBox{
    width: 300px;
    height: 600px;
    margin: 20px auto;
    .el-form-item__content{
      margin-left: 0px !important;
      .subBtn{
        width: 100%;

      }
    }
    input{
      padding-left: 50px;
    }
    .el-form-item__content .ico{
      position: absolute;
      top: 0px;
      left: 0px;
      z-index: 999;
      width: 40px;
      height: 39px;
      text-align: center;
      border-right: 1px solid #ccc;
    }
  }
}
</style>



